@import '../../../style/variables.scss';

.polygon{
    height: 100%;
    :nth-child(odd) {
        background: $bg;
        clip-path: polygon(50% 0, 75% 0, 93% 24%, 98% 65%, 76% 100%, 50% 100%, 21% 100%, 4% 62%, 5% 26%, 25% 0);
    }
    :nth-child(even) {
        background-color: $bg-main;
        clip-path:polygon(49% 0, 78% 0%, 96% 27%, 100% 68%, 77% 100%, 50% 100%, 21% 100%, 7% 66%, 3% 23%, 25% 0%);
    }
    :nth-child(11){
        @include poly(42vw, 10ms, 350px);
    }
    :nth-child(10){
        @include poly(43.4vw, 150ms, 350px);
    }
    :nth-child(9){
        @include poly(47.5vw, 200ms, 350px);
    }
    :nth-child(8){
        @include poly(50vw, 300ms, 350px);
    }
    :nth-child(7){
        @include poly(53vw, 400ms, 350px);
    }
    :nth-child(6){
        @include poly(56.5vw, 500ms, 350px);
    }
    :nth-child(5){
        @include poly(59vw, 600ms, 350px);
    }
    :nth-child(4){
        @include poly(63vw, 700ms, 350px);
    }
    :nth-child(3){
        @include poly(65vw, 800ms, 350px);
    }
    :nth-child(2){
        @include poly(70vw, 900ms, 350px);
    }
    :nth-child(1){
        @include poly(71.5vw, 1000ms, 350px);
    }


    @media (max-width: 1200px) {
        :nth-child(1), :nth-child(2) {
            display: none;
        }
    }
    @media (max-width: 768px) {
        :nth-child(3), :nth-child(4) {
            display: none;
        }
        :nth-child(11){
            @include poly(42vw, 10ms, 300px);
        }
        :nth-child(10){
            @include poly(43.4vw, 150ms, 300px);
        }
        :nth-child(9){
            @include poly(48vw, 200ms, 300px);
        }
        :nth-child(8){
            @include poly(50vw, 300ms, 300px);
        }
        :nth-child(7){
            @include poly(53vw, 300ms, 300px);
        }
        :nth-child(6){
            @include poly(57vw, 500ms, 300px);
        }
        :nth-child(5){
            @include poly(59vw, 600ms, 300px);
        }
    }
    @media (max-width: 568px) {
        :nth-child(5), :nth-child(6) {
            display: none;
        }
        :nth-child(11){
            @include poly(32vw, 10ms, 200px);
        }
        :nth-child(10){
            @include poly(37vw, 150ms, 200px);
        }
        :nth-child(9){
            @include poly(45vw, 200ms, 200px);
        }
        :nth-child(8){
            @include poly(53vw, 300ms, 200px);
        }
        :nth-child(7){
            @include poly(64vw, 300ms, 200px);
        }
    }
    @media (max-width: 425px) {
        :nth-child(7), :nth-child(8) {
            display: none;
        }
    }
    @media (max-width: 375px) {
        :nth-child(9){
            @include poly(37vw, 10ms, 150px);
        }
        :nth-child(10){
            @include poly(45vw, 150ms, 150px);
        }
        :nth-child(11){
            @include poly(58vw, 200ms, 150px);
        }
    }
    @media (max-width: 320px) {
        :nth-child(11){
            @include poly(37vw, 10ms, 130px);
        }
        :nth-child(10){
            @include poly(45vw, 150ms, 130px);
        }
        :nth-child(9){
            @include poly(58vw, 200ms, 130px);
        }
    }
}
